*, *:before, *:after {
 margin: 0;
 padding: 0;
 -moz-box-sizing: border-box;
 box-sizing: border-box;
}

.helix-box {
 text-align: center;
 line-height: 100vh;
 display: inline-block;
}

.helix {
 display: inline-block;
 position: relative;
}

.dots {
 display: inline-block;
 position: relative;
 vertical-align: middle;

 &:not(:last-child){
   margin-right: 6px * 1.5;
 }

 &:before, &:after {
   content: "";
   display: inline-block;
   width: 6px;
   height: 6px;
   border-radius: 50%;

   position: absolute;
 }

 @for $i from 1 through 10 {
   &:nth-child(#{$i}) {
     &:before {
       -webkit-transform: translateY(-200%);
       transform: translateY(-200%);

       -webkit-animation: animBefore 1s linear infinite;
       animation: animBefore 1s linear infinite;

       -webkit-animation-delay: $i*-0.9s;
       animation-delay: $i*-0.9s;

       background-color: $top-dot-color;
     }

     &:after {
       -webkit-transform: translateY(200%);
       transform: translateY(200%);

       -webkit-animation: animAfter 1s linear infinite;
       animation: animAfter 1s linear infinite;

       -webkit-animation-delay: $i*-0.9s;
       animation-delay: $i*-0.9s;

       background-color: $bottom-dot-color;
     }
   }
 }
}

@-webkit-keyframes animBefore {
 0% {
   -webkit-transform: scale(1) translateY(-200%);
   z-index: 1;
 }

 25% {
   -webkit-transform: scale(1.3) translateY(0);
   z-index: 1;
 }

 50% {
   -webkit-transform: scale(1) translateY(200%);
   z-index: -1;
 }

 75% {
   -webkit-transform: scale(0.7) translateY(0);
   z-index: -1;
 }

 100% {
   -webkit-transform: scale(1) translateY(-200%);
   z-index: -1;
 }
}
@keyframes animBefore {
 0% {
   transform: scale(1) translateY(-200%);
   z-index: 1;
 }

 25% {
   transform: scale(1.3) translateY(0);
   z-index: 1;
 }

 50% {
   transform: scale(1) translateY(200%);
   z-index: -1;
 }

 75% {
   transform: scale(0.7) translateY(0);
   z-index: -1;
 }

 100% {
   transform: scale(1) translateY(-200%);
   z-index: -1;
 }
}
@-webkit-keyframes animAfter {
 0% {
   -webkit-transform: scale(1) translateY(200%);
   z-index: -1;
 }

 25% {
   -webkit-transform: scale(0.7) translateY(0);
   z-index: -1;
 }

 50% {
   -webkit-transform: scale(1) translateY(-200%);
   z-index: 1;
 }

 75% {
   -webkit-transform: scale(1.3) translateY(0);
   z-index: 1;
 }

 100% {
   -webkit-transform: scale(1) translateY(200%);
   z-index: 1;
 }
}
@keyframes animAfter {
 0% {
   transform: scale(1) translateY(200%);
   z-index: -1;
 }

 25% {
   transform: scale(0.7) translateY(0);
   z-index: -1;
 }

 50% {
   transform: scale(1) translateY(-200%);
   z-index: 1;
 }

 75% {
   transform: scale(1.3) translateY(0);
   z-index: 1;
 }

 100% {
   transform: scale(1) translateY(200%);
   z-index: 1;
 }
}



